  font-weight: 900; }

body {
  font-family: "Roboto", sans-serif; }

html {
  box-sizing: border-box; }
  html.active {
    overflow: hidden; }

*,
*::before,
*::after {
  box-sizing: inherit; }

img {
  max-width: 100%; }

.wrapper-outer {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: #100e17; }

.wrapper {
  flex-grow: 1;
  flex-basis: auto; }

.footer {
  flex-shrink: 0; }

.img-container {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center; }

.container {
  width: 84.2%;
  margin: 0 auto; }

/* Blocks */
.header-container {
  display: flex;
  align-items: center;
  padding-top: 25px;
  padding-bottom: 25px; }

.logo {
  position: relative;
  width: 7.5%;
  flex-shrink: 0;
  min-width: 90px; }
  .logo::before {
    content: "";
    display: block;
    padding-top: 68.5%; }

.header-logo__image {
  max-width: 100%;
  max-height: 100%; }

.nav {
  margin-left: auto; }

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center; }

.nav-item:not(:last-child) {
  margin-right: 20px; }

.nav-link {
  position: relative;
  overflow: hidden;
  font-size: 1.3rem;
  color: rgba(var(--edthemeli-white), 1);
  text-decoration: none;
  padding-top: 8px;
  padding-right: 4px;
  padding-bottom: 8px;
  padding-left: 4px;
  display: inline-flex; }
  .nav-link::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 0;
    height: 2px;
    background-color: rgba(var(--edthemeli-default), 1);
    transition: 350ms; }
  .nav-link::before {
    content: attr(aria-label);
    position: absolute;
    left: 3px;
    width: auto;
    height: auto;
    overflow: hidden;
    opacity: 0;
    color: rgba(var(--edthemeli-default), 1); }

.nav-link:focus::after {
  width: 100%;
  transition: 350ms; }

.nav-link:focus::before {
  opacity: 1;
  transition: 350ms; }

.nav-link:hover::after {
  width: 100%;
  transition: 350ms; }

.nav-link:hover::before {
  opacity: 1;
  transition: 350ms; }

.resume-btn {
  position: relative;
  overflow: hidden;
  display: inline-flex;
  align-items: center;
  padding-top: 10px;
  padding-right: 20px;
  padding-bottom: 10px;
  padding-left: 20px;
  margin-left: 50px;
  border-radius: 40px;
  text-decoration: none; }
  .resume-btn::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    border-radius: 0;
    display: block;
    height: 100%;
    width: 100%;
    background: rgba(var(--edthemeli-default), 1);
    transition: 350ms; }

.resume-btn:focus::before {
  width: 50px;
  border-radius: 50%;
  transition: 350ms; }

.resume-btn:focus .resume-btn__icon {
  transform: rotate(360deg);
  transition: 350ms; }

.resume-btn:hover::before {
  width: 50px;
  border-radius: 50%;
  transition: 350ms; }

.resume-btn:hover .resume-btn__icon {
  transform: rotate(360deg);
  transition: 350ms; }

.resume-btn__text {
  position: relative;
  z-index: 1;
  font-size: 1rem;
  color: rgba(var(--edthemeli-white), 1);
  text-transform: uppercase;
  margin-right: 10px; }

.resume-btn__icon {
  position: relative;
  z-index: 1;
  fill: rgba(var(--edthemeli-white), 1);
  transition: 350ms;
  transform: rotate(0); }

.header-mobile__btn {
  position: relative;
  display: none;
  width: 25px;
  height: 30px;
  margin-left: auto;
  background-color: transparent;
  border: none;
  padding: 0; }

@media (max-width: 1200px) {
  .header-mobile__btn {
    position: relative;
    z-index: 5;
    display: inline-block;
    transition: 950ms; }
  .header-mobile__btn.active {
    transform: rotate(720deg); }
  .header-mobile__btn-line {
    position: absolute;
    top: 50%;
    right: 0;
    left: 0;
    transform: translateY(-50%);
    transition: 250ms;
    display: inline-flex;
    width: 100%;
    height: 2px;
    background-color: rgba(var(--edthemeli-white), 1); }
  .header-mobile__btn-line__top {
    transform: translateY(calc(-50% - 3px)); }
  .header-mobile__btn-line__bottom {
    transform: translateY(calc(-50% + 3px)); }
  .header-mobile__btn.active .header-mobile__btn-line__top {
    transform: translateY(50%) rotate(45deg); }
  .header-mobile__btn.active .header-mobile__btn-line__bottom {
    transform: translateY(50%) rotate(-45deg); }
  .nav {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    transform: translateX(-100%);
    overflow-y: auto;
    transition: 500ms; }
    .nav::before, .nav::after {
      content: "";
      position: absolute;
      z-index: -1;
      height: calc(50% + 1px);
      width: 100%;
      left: 0;
      transform: translateX(-100%);
      display: block;
      background-color: rgba(var(--edthemeli-default), 1);
      transition: 450ms; }
    .nav::before {
      top: 0; }
    .nav::after {
      bottom: 0;
      transition-delay: 150ms; }
  .nav.active {
    transform: translateX(0);
    transition-delay: 500ms; }
    .nav.active::before {
      transform: translateX(0); }
    .nav.active::after {
      transform: translateX(0);
      transition-delay: 600ms; }
    .nav.active .nav-list {
      opacity: 1;
      transition-delay: 700ms; }
  .nav-list {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
    opacity: 0;
    transition-delay: unset; }
  .nav-item:not(:last-child) {
    margin-right: 0; }
  .nav-link {
    font-size: 3.2rem;
    padding-top: 10px;
    padding-bottom: 10px;
    font-weight: 900;
    color: rgba(var(--edthemeli-white), 0.8);
    transition-delay: 150ms; }
  .nav-link:hover {
    color: rgba(var(--edthemeli-white), 1); }
  .nav-link::before,
  .nav-link::after {
    content: "";
    top: 10px;
    right: 0;
    left: 0;
    height: calc(50% - 8px);
    width: 100%;
    transform: translateX(0);
    transition: none;
    background-color: transparent;
    opacity: 1; }
  .nav-link::after {
    top: auto;
    bottom: 10px; }
  .nav-link:hover::after {
    transition: transform 400ms;
    transition-delay: 100ms; }
  .nav-link:hover::before {
    transition: transform 500ms; }
  .nav-link:hover::after,
  .nav-link:hover::before {
    transform: translateX(100%);
    background-color: rgba(var(--edthemeli-white), 1); }
  .resume-btn {
    display: none; } }
